@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Ubuntu', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
  /* display: flex; */
   flex-direction: row;
   position: -webkit-sticky;
   position: sticky;
   background-color: #F2F3F4;
   border-bottom: solid 1px #EAEDED;
}
.header--left,
.header--right {
  display: inline-block;
  vertical-align: middle;
}
.brand {
  display: flex; }
  .brand .brand__name {
    text-align: center;
    margin-top: 10px;
    font-weight: bold;
    font-size: 25px;
    padding: 5px 10px; }
  .brand .brand__svg {
    height: 40px;
    width: 40px;
    padding: 10px; }
    .brand .brand__svg .brand__rect {
      -webkit-transition: -webkit-transform 1000ms ease;
      transition: -webkit-transform 1000ms ease;
      transition: transform 1000ms ease;
      transition: transform 1000ms ease, -webkit-transform 1000ms ease;
      -webkit-transform-origin: center center;
              transform-origin: center center;
      position: relative; }
    .brand .brand__svg:hover .brand__rect {
      -webkit-transform: rotateX(180deg);
              transform: rotateX(180deg); }
    .brand .brand__svg .brand__rect.brand__rect--1 {
      -webkit-transition-delay: 1400ms;
              transition-delay: 1400ms; }
    .brand .brand__svg .brand__rect.brand__rect--2 {
      -webkit-transition-delay: 1200ms;
              transition-delay: 1200ms; }
    .brand .brand__svg .brand__rect.brand__rect--3 {
      -webkit-transition-delay: 1000ms;
              transition-delay: 1000ms; }
    .brand .brand__svg .brand__rect.brand__rect--4 {
      -webkit-transition-delay: 800ms;
              transition-delay: 800ms; }
    .brand .brand__svg .brand__rect.brand__rect--5 {
      -webkit-transition-delay: 600ms;
              transition-delay: 600ms; }
    .brand .brand__svg .brand__rect.brand__rect--6 {
      -webkit-transition-delay: 400ms;
              transition-delay: 400ms; }
    .brand .brand__svg .brand__rect.brand__rect--7 {
      -webkit-transition-delay: 200ms;
              transition-delay: 200ms; }
    .brand .brand__svg .brand__rect.brand__rect--8 {
      -webkit-transition-delay: 0ms;
              transition-delay: 0ms; }
  .brand .brand__svg:hover .brand__rect.brand__rect--8 {
    -webkit-transition-delay: 0ms;
            transition-delay: 0ms; }
  .brand .brand__svg:hover .brand__rect.brand__rect--7 {
    -webkit-transition-delay: 200ms;
            transition-delay: 200ms; }
  .brand .brand__svg:hover .brand__rect.brand__rect--6 {
    -webkit-transition-delay: 400ms;
            transition-delay: 400ms; }
  .brand .brand__svg:hover .brand__rect.brand__rect--5 {
    -webkit-transition-delay: 600ms;
            transition-delay: 600ms; }
  .brand .brand__svg:hover .brand__rect.brand__rect--4 {
    -webkit-transition-delay: 800ms;
            transition-delay: 800ms; }
  .brand .brand__svg:hover .brand__rect.brand__rect--3 {
    -webkit-transition-delay: 1000ms;
            transition-delay: 1000ms; }
  .brand .brand__svg:hover .brand__rect.brand__rect--2 {
    -webkit-transition-delay: 1200ms;
            transition-delay: 1200ms; }
  .brand .brand__svg:hover .brand__rect.brand__rect--1 {
    -webkit-transition-delay: 1400ms;
            transition-delay: 1400ms; }

